export default {
  name: "cha",
  data() {
    return {
      relase_loading: false,
      provinces_list: undefined,
      city_list: undefined,
      post_success: false,
      info_from_list: ["", ""],
      service_list: ["", "", "", "", "", "69", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "sm", "", "", "", ""],
      zh_type_list: [
        {
          'abbr': 2,
          'state': "/"
        }, {
          'abbr': 1,
          'state': "/"
        }
      ],
      release_data: {
        province: "",
        city: "",
        zh_type: 2,
        services: [],
        info_from: "",
        upload_image_list: []
      },
      tmp_upload_host_conf: undefined,
      post_success_tip: "",
      release_xx_permission: true,
      user_abnormal: false
    };
  },
  created() {
    window._t = this;
    this.init();
    if (!this.$store.state.hasLogin) {
      this.show_no_login_dialog('', true);
    } else {

      var tracker = this.get_tracker();


      var register_time = this.$store.state.user.userinfo.register_time;

      var user_permission = this.$store.state.user.userinfo.permission;
      console.log('user_permission:', user_permission);
      if (user_permission) {
        this.release_xx_permission = user_permission['release:xx'];
        if (!this.release_xx_permission && tracker) {
          tracker.send("event", 'release-cha-limit', 'release_xx_permission', this.$store.state.user.userinfo.uid);
        }
      }

      var abnormal = this.$store.state.user.userinfo.abnormal;
      console.log('abnormal:', abnormal);
      if (abnormal) {
        this.user_abnormal = true;
        if (tracker) {
          tracker.send("event", 'release-cha-limit', 'abnormal', this.$store.state.user.userinfo.uid);
        }
      }
    }
    window._t = this;
  },
  destroyed() {
    if (this.listener_init_lister) {
      this.listener_init_lister.off();
    }
  },
  computed: {
    upload_image_url: function () {
      if (this.tmp_upload_host_conf) {
        var image_host = this.tmp_upload_host_conf.host;
        if (this.tmp_upload_host_conf.hosts) {
          image_host = this.tmp_upload_host_conf.hosts[0];
        }
        let update_url = 'https://' + image_host + '/upload/tmp_image';
        console.log('update_url:', update_url);
        return update_url;
      }
      return '';
    }
  },
  methods: {
    on_removed: function (files) {
      console.log('on_removed:', files);
    },
    on_upload_success: function (files) {
      console.log('on_upload_success:', files);
      var response = JSON.parse(files.xhr.response);
      console.log('response:', response);
      this.release_data.upload_image_list.push(response['data']);
    },
    update_province_select: function (v) {
      console.log("province_chanage:", v);
      if (v) {
        this.release_data.province = v;
      }
      this.city_list = this.region_conf[this.release_data.province];
      this.release_data.city = this.city_list[0];
    },
    update_data: function () {
      this.tmp_upload_host_conf = this.$store.state.init.data.tmp_upload_host_conf;
      this.provinces_list = this.$store.state.init.data.provinces_list;
      this.region_conf = this.$store.state.init.data.region_conf;
      this.update_province_select();
    },
    init: function () {
      this.listener_init_lister = this.$hub.on('event:init', () => {
        this.update_data();
        this.$forceUpdate();
      });
      this.update_data();
    }, release_click: function () {
      var release_data = this.release_data;
      if (!this.release_data.title || this.release_data.title.length < 1) {
        this.$message('', {
          theme: "error",
          position: "top-left",
          duration: 5000
        });
        return false;
      }
      if (release_data.province.length <= 0 || release_data.city.length <= 0) {
        this.$message('', {
          theme: "error",
          position: "top-left",
          duration: 5000
        });
        return false;
      }
      if (!this.release_data.zh_type || this.release_data.zh_type.length < 1) {
        this.$message('', {
          theme: "error",
          position: "top-left",
          duration: 5000
        });
        return false;
      }
      if (!this.release_data.info_from || this.release_data.info_from.length < 1) {
        this.$message('', {
          theme: "error",
          position: "top-left",
          duration: 5000
        });
        return false;
      }
      if (!this.release_data.age || this.release_data.age.length < 1) {
        this.$message('', {
          theme: "error",
          position: "top-left",
          duration: 5000
        });
        return false;
      }
      if (!this.release_data.price || this.release_data.price.length < 1) {
        this.$message('', {
          theme: "error",
          position: "top-left",
          duration: 5000
        });
        return false;
      }
      if (!release_data.qq && !release_data.wechat && !release_data.phone) {
        this.$message('', {
          theme: "error",
          position: "top-left",
          duration: 5000
        });
        return false;
      }
      if (!this.release_data.services || this.release_data.services.length < 1) {
        this.$message('', {
          theme: "error",
          position: "top-left",
          duration: 5000
        });
        return false;
      }
      if (!this.release_data.content || this.release_data.content.length < 6) {
        this.$message('', {
          theme: "error",
          position: "top-left",
          duration: 5000
        });
        return false;
      }

      var post_data = {
        release_data: this.release_data
      }

      this.relase_loading = true;
      this.$axios.post('/app/xx/release', post_data).then((response) => {

        this.post_success = true;
      }).catch((error) => {
        console.log('error:', error);
        this.relase_loading = false;
      });
    },

  }
}
